<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">订单审核</div>
      <div class="modal-body">
        <h3>基本信息</h3>
        <div class="row">
          <div class="columns">
            <span class="label">订单ID:</span>
            <span class="value">{{ form.order_id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">订单号:</span>
            <span class="value">{{ form.order_number }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">货件号:</span>
            <span class="value">{{ form.posting_number }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">运单号:</span>
            <span class="value">{{ form.tracking_number?form.tracking_number:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">系统状态:</span>
            <span class="value">{{ form.system_status?form.system_status:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">开始处理时间:</span>
            <span class="value">{{ in_process_at_time?in_process_at_time:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">必须收取时间:</span>
            <span class="value">{{ shipment_date?shipment_date:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">订单总价:</span>
            <span class="value">{{ form.total_price }}</span>
          </div>
          <div class="columns">
            <span class="label">价格货币:</span>
            <span class="value">{{ form.currency_code }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">店铺名称:</span>
            <span class="value">{{ form.shop?form.shop.name:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">负责人员:</span>
            <span class="value">{{ form.owner?form.owner.name:"暂无" }}</span>
          </div>
        </div>
        
        <h3 style="margin-top: 10px;">产品信息</h3>
        <div v-for="(product, index) in form.products" :key="index" style="display: flex;">
          <span style="margin-right: 8px;">{{ index+1 }}.</span>
          <div>
            <div class="row">
              <div class="columns">
                <span class="label">产品名称:</span>
                <span class="value">{{ product.name }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">offer_id:</span>
                <span class="value">{{ product.offer_id }}</span>
              </div>
              <div class="columns">
                <span class="label">sku:</span>
                <span class="value">{{ product.sku }}</span>
              </div>
              <div class="columns">
                <span class="label">product_id:</span>
                <span class="value">{{ product.product_id }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">产品价格:</span>
                <span class="value">{{ product.price }}</span>
              </div>
              <div class="columns">
                <span class="label">产品数量:</span>
                <span class="value">{{ product.quantity }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">fbo佣金百分比:</span>
                <span class="value">{{ product.fbo_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbo佣金额:</span>
                <span class="value">{{ product.fbo_commission_value }}</span>
              </div>
              <div class="columns">
                <span class="label">fbp佣金百分比:</span>
                <span class="value">{{ product.fbp_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbp佣金额:</span>
                <span class="value">{{ product.fbp_commission_value }}</span>
              </div>
            </div>

            <div class="row">

              <div class="columns">
                <span class="label">fbs佣金百分比:</span>
                <span class="value">{{ product.fbs_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbs佣金额:</span>
                <span class="value">{{ product.fbs_commission_value }}</span>
              </div>
              <div class="columns">
                <span class="label">rfbs佣金百分比:</span>
                <span class="value">{{ product.rfbs_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">rfbs佣金额:</span>
                <span class="value">{{ product.rfbs_commission_value }}</span>
              </div>
              
            </div>
          </div>  
        </div>

        <h3 style="margin-top: 10px;">产品拆分</h3>
        <div v-for="(order_package, index_package) in order_packages" :key="index_package+100" style="display: flex;">
          <span style="margin-right: 8px; display: flex; ;align-items: center;">{{ index_package+1 }}号包裹.</span>
          <div>
            <div v-for="(product,index_product) in order_package.products" :key="index_product+1000">
              <div class="row">
                <a-select 
                  v-model="product.product_id" 
                  placeholder="请选择" 
                  style="width: 200px;"
                  @change="onChange"
                > 
                  <a-select-option v-for="option in options" :value="option.product_id" :key="option.product_id">{{ option.offer_id }}</a-select-option> 
                </a-select> 
                <div class="columns">
                  <span class="label" style="margin-left: 15px;">产品数量:</span>
                  <input v-model.number="product.quantity">
                </div>
                <a-button type="danger" icon="delete" size="small"  @click="() => delproduct(order_package,index_product)" style="margin-left: 15px; width: 100px;">删除产品</a-button>
              </div>
            </div>
            <a-button icon="edit" size="small" @click="() => addProduct(order_package)" style="margin-top: 5px;">新增产品</a-button>
            <a-button type="danger" icon="delete" size="small"  @click="() => delpackage(index_package)" style="margin-top: 5px;">删除打包袋</a-button>
          </div>  
        </div>
        <a-button icon="edit" size="small" @click="addPackage" style="margin-left: 500px;">新增打包袋</a-button>
    </div>
    </a-modal>
  </div>
</template>

<script>
import moment from 'moment'
import {auditOzonOrder } from '@/api/operation'
import { message } from "ant-design-vue";

export default {
  name: 'ApprovalFormMoal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      order_packages : [],
    };
  },
  mounted(){
    
  },
  watch: {
    "visible":{
      handler(newValue){
        if (newValue == true){
          this.order_packages = [
            {
              "products": this.form.products.map(item => {
                return {
                  "product_id": item.sku,
                  "quantity": item.quantity
                }
              })
            }
          ]
        }
      },
      deep:true
    }

  },
  computed: {
    options(){
      let value = []
      for(let item in this.form.products){
        value.push({"offer_id":this.form.products[item]["offer_id"],"product_id":this.form.products[item]["sku"]})
      }
      return value
    },
    in_process_at_time(){
      return moment(this.form.in_process_at).format('YYYY-MM-DD HH:mm:ss')
    },
    shipment_date(){
      return moment(this.form.shipment_date).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  methods: {
    addProduct(order_package){ 
      order_package.products.push({
        "product_id":null,
        "quantity":0
      })
    },
    addPackage(){
      this.order_packages.push({
        "products":[]
      })
    },
    delproduct(order_package,index_product){
      order_package.products.splice(index_product,1)
    },
    delpackage(index_package){
      this.order_packages.splice(index_package,1)
    },
    confirm() {
        // 检验同一包裹内打包数据是否重复 / 打包数据为0
        let duplicateFound = false;

        this.order_packages.some(pkg => {
          let seen = new Set();
          pkg.products.some(product => {

            if (!product.product_id) {
              alert(`产品id不能为空！`);
              duplicateFound = true;
              return true
            }

            if (product.quantity == "0") {
              alert(`产品数量不能为空！`);
              duplicateFound = true;
              return true
            }
            if (seen.has(product.product_id)) {
              alert("同一包裹内不能存在多个相同产品！请检查后重试！");
              duplicateFound = true;
              return true
            }
            seen.add(product.product_id);
          });
          if (duplicateFound) { 
            return true
          }
        });

        if (duplicateFound) { 
          return;
        }

        // 检验拆分总数是否一致
        let merged = {};
        this.order_packages.forEach(pkg => {
          pkg.products.forEach(product => {
            if (merged[product.product_id]) {
              merged[product.product_id] += parseInt(product.quantity);
            } else {
              merged[product.product_id] = parseInt(product.quantity);
            }
          });
        });
        this.form.products.some(product => {
          if (product["quantity"] != merged[product["sku"]]) {
            alert(`${product["offer_id"]}产品 分包总数与订单总数不一致！请检查后重试！`)
            duplicateFound = true;
            return true
          } 
        })
        if (duplicateFound) {
          return;
        }
        auditOzonOrder({"posting_number":this.form.posting_number,"packages":this.order_packages}).then(
          res => {
            message.success(res.msg)
            this.order_packages = [];
            this.$emit('update', false);
        })
    },
    cancel() {
      this.order_packages = []
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns .label{
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}

button {
  margin-right: 2px;
}
</style>